      if(this.form.extraAge[0] !== '' || this.form.extraAge[1] !== ''){
        this.formSelectItem.idage = `min:${this.form.extraAge[0]}/max:${this.form.extraAge[1]}`
      }
      
    },
  },
  data () {
    return {
      coltr:'col-3',
      colsi:'col-6',
      areaList: [],
      areaOpt: [],
      currentArea:'',
      customizeDataAll:{   //過濾資料用
        age: ['idage','birthmonth','agemonth'],
        info: ['sex','home_addr1','home_addr2','home_addr3','groups','custrl','marry','edu','job'],
        financial: ['incomy','family_incom','invest','finance','house','car'],
        service: ['web_member','online','mobile','payment','email','trans_plpy','ecall','policy_loan','claimoffset'],
        isInsure: ['prod_codes'],
        notInsure: ['prod_codes_n'],
        futurePayment: ['matage_month','annuity_month'],
        payment: ['pla1_month','plc1_month','plf1_month'],
      },
      formSelectItem: {
        idage:'',
        agemonth:'',
        custrl:'',
        job:'',
        family_incom:'',
        finance:[], //Array
        invest:'',
        marry:'',
        edu:'',
        house:'',
        car:'',
        web_member:'',
        online:'',
        mobile:'',
        payment:'',
        email:'',
        sex:'',
        trans_plpy:'',
        ecall:'',
        policy_loan:'',
        claimoffset:'',
        matage_month:'',
        annuity_month:'',
        pla1_month:'',
        plc1_month:'',
        plf1_month:'',
        birthmonth:'',
        incomy:'',
        home_addr:{
          city:'',
          zip:'',
          street:'',
        },
        groups:'',
        prod_codes:{
          main:'',
          atta:'',
        },
        prod_codes_n:{
          main:'',
          atta:'',
        },
      },
      form: {
        name: '我的組合',
        addFavrite: false,
        favriteName: '',
        extraAge: ['', ''],
        age: 0,
        gender: '',
        birthMonth: '',
        addAge: '',
        mainDoc: '',
        subDoc: '',
        liveArea: ['', ''],
        customerType: ['', ''],
        payment: []
      }
    }
  },
  methods: {
    formDataChange(val) {
      this.form = {
        name: '我的組合',
        addFavrite: false,
        favriteName: '',
        extraAge: ['', ''],
        age: 0,
        gender: '',
        birthMonth: '',
        addAge: '',
        mainDoc: '',
        subDoc: '',
        liveArea: ['', ''],
        customerType: ['', ''],
        payment: [],
        ...val
      }
      this.formSelectItem = {
        idage:'',
        agemonth:'',
        custrl:'',
        job:'',
        family_incom:'',
        finance:[], //Array
        invest:'',
        marry:'',
        edu:'',
        house:'',
        car:'',
        web_member:'',
        online:'',
        mobile:'',
        payment:'',
        email:'',
        sex:'',
        trans_plpy:'',
        ecall:'',
        policy_loan:'',
        claimoffset:'',
        matage_month:'',
        annuity_month:'',
        pla1_month:'',
        plc1_month:'',
        plf1_month:'',
        birthmonth:'',
        incomy:'',
        home_addr:{
          city:'',
          zip:'',
          street:'',
        },
        groups:'',
        prod_codes:{
          main:'',
          atta:'',
        },
        prod_codes_n:{
          main:'',
          atta:'',
        },
      }
      
      
      let fa_field_name = this.favoriteData.field_name.split('-')
      let fa_option = this.favoriteData.option.split('-')
      let fa_option_ch = this.favoriteData.option_ch.split(';')
      fa_field_name.forEach((key,index) => {
        if(key === 'finance'){  //過濾投資工具
          this.formSelectItem.finance = fa_option[index].replace(/0/g,'').split('').map(item => {return Number(item)})
        }else if (key === 'home_addr1'){   //過濾縣市
          this.formSelectItem.home_addr.city = fa_option[index]
        }else if (key === 'home_addr2'){  //過濾地區
          this.changeCity(fa_option[index])
        }else if (key === 'home_addr3'){   //過濾地址
          this.formSelectItem.home_addr.street = fa_option[index]
        }else if (key === 'prod_codes'){   //過濾已投保
          let MA = `${fa_option_ch[index].indexOf('主約') > -1 ? 'M' : ''}${fa_option_ch[index].indexOf('附約') > -1 ? 'A' : ''}`
          if(MA == 'M'){
            this.formSelectItem.prod_codes.main = fa_option[index]
          }else if(MA == 'A'){
            this.formSelectItem.prod_codes.atta = fa_option[index]
          }else if(MA == 'MA'){
            this.formSelectItem.prod_codes.main = fa_option[index].split(':')[0]
            this.formSelectItem.prod_codes.atta = fa_option[index].split(':')[1]
          }
        }else if (key === 'prod_codes_n'){   //過濾未投保
          let MA = `${fa_option_ch[index].indexOf('主約') > -1 ? 'M' : ''}${fa_option_ch[index].indexOf('附約') > -1 ? 'A' : ''}`
          if(MA == 'M'){
            this.formSelectItem.prod_codes_n.main = fa_option[index]
          }else if(MA == 'A'){
            this.formSelectItem.prod_codes_n.atta = fa_option[index]
          }else if(MA == 'MA'){
            this.formSelectItem.prod_codes_n.main = fa_option[index].split(':')[0]
            this.formSelectItem.prod_codes_n.atta = fa_option[index].split(':')[1]
          }
        }else if (key === 'idage'){   //年紀
          let setByUser = ''
          this.formItemData.formSelect.idage.forEach(item => {
            if(item.option === fa_option[index]){
              setByUser = setByUser + '1'
            }
          })
          if(setByUser === ''){
            let getNewNum = fa_option[index].replace(/.{3}\:/g,'').split('/')
            
            
            this.form.extraAge[0] = getNewNum[0]
            this.form.extraAge[1] = getNewNum[1]
            this.formSelectItem.idage = fa_option[index]
          }else{
            this.formSelectItem.idage = fa_option[index]
          }
        }else{
          this.formSelectItem[key] = fa_option[index]
        }
      });
      this.checkSelect()
    },
    changeCity (key = '') {
      this.formSelectItem.home_addr.zip = ''
      this.formSelectItem.home_addr.street = ''
      if(this.formSelectItem.home_addr.city == ''){
        this.areaOpt = []
      }else{
        queryDistByCity(this.$store.state.userInfo.id, this.formSelectItem.home_addr.city).then(result => {
          if(result.body.cityAreaList) {  
            this.areaOpt = result.body.cityAreaList.map(item => { return {area: item.AREA, city:item.CITY, zipcode:item.ZIPCODE}})
          } else {
            this.areaOpt = []
          }
          this.formSelectItem.home_addr.zip = key
        })
      }
    },
    checkSelect(){
      let hasSelect = []
      const DataAll = this.customizeDataAll
      const SelectString = this.favoriteData.field_name
      const toggleItem = ['age','info','financial','service','isInsure','notInsure','futurePayment','payment']
      
      for(let i in DataAll){
        let hasVal = false
        DataAll[i].forEach(val => {
          if(SelectString.indexOf(val) > -1){
            hasVal = true
          }
        })
        if(hasVal){
          hasSelect.push(i)
        }
      }
      
      document.querySelectorAll('.panel-header').forEach(element => {
        
        if (element.nextSibling.classList.contains('in')) {
          element.classList.remove('is-in')
          element.nextSibling.classList.remove('in')
        } 
      });
      hasSelect.forEach(item => {
        document.querySelector(`.${item}`).classList.add('is-in')
        document.querySelector(`.${item}`).nextSibling.classList.add('in')
      })
      
    }
  },
  mounted () {
    document.querySelectorAll('.panel-header').forEach(element => {
      element.addEventListener('click', function () {
        if (this.nextSibling.classList.contains('in')) {
          this.classList.remove('is-in')
          this.nextSibling.classList.remove('in')
        } else {
          this.classList.add('is-in')
          this.nextSibling.classList.add('in')
        }
      })
    });
  }
} 
</script>
