// Function to make IE9+ support forEach:
// 使 NodeList 支援 forEach 方法
(function() {
  if (typeof NodeList.prototype.forEach === "function")
      return false;
  else
      NodeList.prototype.forEach = Array.prototype.forEach;
})();

export default {
  name: 'SearchFilter',
  mixins: [commonMixin],
  components: {CustomizeFilter, FavoriteModal, ConfirmDelete,WarningModal},
  computed: {
