      mainPlanOpt: [],
      secondaryPlanOpt: [],
      deleteItem: null,
      formValid: true,
      favoriteModalOpen: false,
      customizeModalOpen: false,
      confirmDeleteOpen: false,
      warningModalOpen:false,
      selectedFavo: null,
      formSelect :{
        idage:[],
