        job:[],
        family_incom:[],
        finance:[],
        invest:[],
        marry:[],
        edu:[],
        house:[],
        car:[],
        web_member:[],
        online:[],
        mobile:[],
        payment:[],
        email:[],
        sex:[],
        trans_plpy:[],
        ecall:[],
        policy_loan:[],
        claimoffset:[],
        matage_month:[],
        annuity_month:[],
        pla1_month:[],
        plc1_month:[],
        plf1_month:[],
        birthmonth:[],
        incomy:[],
        home_addr_zip:[],
        groups:[],
        prod_codes:[],
        prod_codes_n:[],
        home_addr3:[],
      },
      formSelectItem: {
        idage:'',
        agemonth:'',
        custrl:'',
        job:'',
        family_incom:'',
        finance:[], //Array
        invest:'',
        marry:'',
        edu:'',
        house:'',
        car:'',
        web_member:'',
        online:'',
        mobile:'',
        payment:'',
        email:'',
        sex:'',
        trans_plpy:'',
        ecall:'',
        policy_loan:'',
        claimoffset:'',
        matage_month:'',
        annuity_month:'',
        pla1_month:'',
        plc1_month:'',
        plf1_month:'',
        birthmonth:'',
        incomy:'',
        home_addr:{
          city:'',
          zip:'',
          street:'',
        },
        groups:'',
        prod_codes:{
          main:'',
          atta:'',
        },
        prod_codes_n:{
          main:'',
