        },
      },
      submitDisable: true,
      form: {
        addFavrite: false,
        favriteName: '',
        extraAge: ['', ''],
        age: 0,
        gender: '',
        group: '',
        birthMonth: '',
        addAge: '',
        // 基本資料
        gender: '',
        group: '',
