        // 服務申辦項目
        service_company: '',
        service_updateonline: '',
        service_mobile: '',
        service_onefinger: '',
        service_ebill: '',
        service_transfer: '',
        service_eazycall: '',
        service_insureloan: '',
        service_settlement: '',
        // 商品行銷
        mainDoc: '',
        subDoc: '',
        not_insure_mainDoc: '',
        not_insure_subDoc: '',
        // 契約事件
        payment_1: '',
        payment_2: '',
        insurance_accident: '',
        insurance_car: '',
        insurance_fire: '',
        liveArea: ['', ''],
        customerType: ['', ''],
        payment: []
      },
      records:[],
      favorites:[],
      customizeDataAll:{
        age: ['idage','birthmonth','agemonth'],
        info: ['sex','home_addr','groups','custrl','marry','edu','job'],
        financial: ['incomy','family_incom','invest','finance','house','car'],
        service: ['web_member','online','mobile','payment','email','trans_plpy','ecall','policy_loan','claimoffset'],
        isInsure: ['prod_codes_main','prod_codes_atta'],
        notInsure: ['prod_codes_n_main','prod_codes_n_atta'],
        futurePayment: ['matage_month','annuity_month'],
        payment: ['pla1_month','plc1_month','plf1_month'],
      },
      customizeDataDefault:{
        age: ['idage','birthmonth','agemonth'],
        info: ['home_addr','custrl'],
        financial: [],
        service: [],
        isInsure: [],
        notInsure: ['prod_codes_n_main','prod_codes_n_atta'],
        futurePayment: ['matage_month'],
        payment: ['plc1_month','plf1_month'],
      },
      customizeData:{
        age: [],
        info: [],
        financial: [],
        service: [],
        isInsure: [],
        notInsure: [],
        futurePayment: [],
        payment: [],
      },
    }  
  },
  watch:{
    formSelectItem: {  // 判斷使用者是否有選擇條件
      handler(newVal) {
        let hasVal = false
        for(let item in newVal){
          let val = newVal[item]
          if(typeof val == 'object') {
            for(let j in val) {
              if(val[j] !== '') {
                hasVal = true
              }
            }
          }else if(typeof val == 'array' && val.length > 0) {
            hasVal = true
          }else if(val !== ''){
            hasVal = true
          }
        }
        this.submitDisable = !hasVal
      },
      deep: true
    },
    'form.extraAge': function(){
      if(this.form.extraAge[0] == '' && this.form.extraAge[1] == ''){
        this.formSelectItem.idage = ''
      }else{
        this.formSelectItem.idage = `min:${this.form.extraAge[0]}/max:${this.form.extraAge[1]}`
      }
      
    },
    groupsVal(){ // 雙向綁定 v-select 元件 群組 model
      if(this.groupsVal !== null){
        this.formSelectItem.groups = this.groupsVal.trim()
      }else{
        this.formSelectItem.groups = ''
      }
    },
    'formSelectItem.groups': function(newVal,oldVal){
      this.groupsVal = this.formSelectItem.groups
    },
    payMainPlanVal(){ // 雙向綁定 v-select 元件 投保商品 model
      if(this.payMainPlanVal !== null){
        this.formSelectItem.prod_codes.main = this.payMainPlanVal.trim()
      }else{
        this.formSelectItem.prod_codes.main = ''
      }
    },
    'formSelectItem.prod_codes.main': function(newVal,oldVal){
      this.payMainPlanVal = this.formSelectItem.prod_codes.main
    },
    payAttaPlanVal(){ // 雙向綁定 v-select 元件 投保商品 model
      if(this.payAttaPlanVal !== null){
        this.formSelectItem.prod_codes.atta = this.payAttaPlanVal.trim()
      }else{
        this.formSelectItem.prod_codes.atta = ''
      }
    },
    'formSelectItem.prod_codes.atta': function(newVal,oldVal){
      this.payAttaPlanVal = this.formSelectItem.prod_codes.atta
    },
    noMainPlanVal(){ // 雙向綁定 v-select 元件 投保商品 model
      if(this.noMainPlanVal !== null){
        this.formSelectItem.prod_codes_n.main = this.noMainPlanVal.trim()
      }else{
        this.formSelectItem.prod_codes_n.main = ''
      }
    },
    'formSelectItem.prod_codes_n.main': function(newVal,oldVal){
      this.noMainPlanVal = this.formSelectItem.prod_codes_n.main
    },
    noAttaPlanVal(){ // 雙向綁定 v-select 元件 投保商品 model
       if(this.noAttaPlanVal !== null){
        this.formSelectItem.prod_codes_n.atta = this.noAttaPlanVal.trim()
      }else{
        this.formSelectItem.prod_codes_n.atta = ''
      }
    },
    'formSelectItem.prod_codes_n.atta': function(newVal,oldVal){
      this.noAttaPlanVal = this.formSelectItem.prod_codes_n.atta
    },
  },
  methods: {
    ...mapActions([
      'setPageLock'
    ]),
    checkQueryAndSetOpt(){  
      let filterData = []
      for (let i in this.$route.query){
        filterData.push(this.$route.query[i])
      }
      
      let field_name = []
      let option = []
      let option_ch = []
      

      filterData.forEach(item => {
        field_name.push(item.field_name)
        option.push(item.option)
        option_ch.push(item.title)
      })
      
      field_name.forEach((key,index) => {
        if(key === 'finance'){  //過濾投資工具
          this.formSelectItem.finance = option[index].replace(/0/g,'').split('').map(item => {return Number(item)})
        }else if (key === 'groups'){   //過濾群組
          this.formSelectItem.groups = option_ch[index].replace('所屬群組:','')
        }else if (key === 'home_addr1'){   //過濾縣市
          this.formSelectItem.home_addr.city = option[index]

        }else if (key === 'home_addr2'){  //過濾地區
          this.formSelectItem.home_addr.zip = option[index]
        }else if (key === 'home_addr3'){   //過濾地址
          this.formSelectItem.home_addr.street = option[index]
        }else if (key === 'prod_codes'){   //過濾已投保
          let MA = `${option_ch[index].indexOf('主約') > -1 ? 'M' : ''}${option_ch[index].indexOf('附約') > -1 ? 'A' : ''}`
          if(MA == 'M'){
            this.formSelectItem.prod_codes.main = option_ch[index].replace('已投商品:主約:','')
          }else if(MA == 'A'){
            this.formSelectItem.prod_codes.atta = option_ch[index].replace('已投商品:附約:','')
          }else if(MA == 'MA'){
            let dataAry = option_ch[index].split('附約:')
            this.formSelectItem.prod_codes.main = dataAry[0].replace('已投商品:主約:','')
            this.formSelectItem.prod_codes.atta = dataAry[1]
          }
        }else if (key === 'prod_codes_n'){   //過濾未投保
          let MA = `${option_ch[index].indexOf('主約') > -1 ? 'M' : ''}${option_ch[index].indexOf('附約') > -1 ? 'A' : ''}`
          if(MA == 'M'){
            this.formSelectItem.prod_codes_n.main = option_ch[index].replace('未投商品:主約:','')
          }else if(MA == 'A'){
            this.formSelectItem.prod_codes_n.atta = option_ch[index].replace('未投商品:附約:','')
          }else if(MA == 'MA'){
            let dataAry = option_ch[index].split('附約:')
            this.formSelectItem.prod_codes_n.main = dataAry[0].replace('未投商品:主約:','')
            this.formSelectItem.prod_codes_n.atta = dataAry[1]
          }
        }else if (key === 'idage'){   //年紀
          let setByUser = ''
          window.aaa = this.formSelect.idage

          this.formSelect.idage.forEach(item => {
            if(item.option === option[index]){
              setByUser = setByUser + '1'
            }
          })
          
          if(setByUser === ''){
            let getNewNum = option[index].replace(/.{3}\:/g,'').split('/')
            this.form.extraAge[0] = getNewNum[0]
            this.form.extraAge[1] = getNewNum[1]
            this.formSelectItem.idage = option[index]
          }else{
            this.formSelectItem.idage = option[index]
          }
        }else{
          this.formSelectItem[key] = option[index]
        }
        
      });
      this.changeCity(this.formSelectItem.home_addr.city)
      this.queryArray.forEach(item => {
        if(item.field_name === 'home_addr2'){
          this.formSelectItem.home_addr.zip = item.option
        }else if(item.field_name === 'home_addr3'){
          this.formSelectItem.home_addr.street = item.option
        }
      })
      
    },
    initSearchFilter() {
      this.filterLoading = true
      getSearchFilter(this.$store.state.userInfo.id).then(result => {
        this.filterLoading = false
        if(result.body && result.body.conditions_list){
          result.body.conditions_list.forEach(item => {
            this.formSelect[item.field_name].push(item);
          })
        }
        this.formSelect.birthmonth = this.formSelect.birthmonth.sort(function (a, b) {return a.ser_no - b.ser_no});
        if(this.queryArray[0] && this.queryArray[0].hasOwnProperty('title') && false){  //如果是從 filter result 頁面回來 (現在強制false關掉此功能)
          this.queryArray.forEach(item => {
            for(let filterName in this.customizeDataAll){
              if(this.customizeDataAll[filterName].indexOf(item.field_name) !== -1){
                this.customizeData[filterName].push(item.field_name)
              }
            }
            if(item.field_name === 'prod_codes'){
              if(item.title.indexOf('主約') > -1){
                this.customizeData.isInsure.push('prod_codes_main')
              }
              if(item.title.indexOf('附約') > -1){
                this.customizeData.isInsure.push('prod_codes_atta')
              }
            }else if(item.field_name === 'prod_codes_n'){
              if(item.title.indexOf('主約') > -1){
                this.customizeData.notInsure.push('prod_codes_n_main')
              }
              if(item.title.indexOf('附約') > -1){
                this.customizeData.notInsure.push('prod_codes_n_atta')
              }
            }else if(item.field_name === 'home_addr1'){
              this.customizeData.info.push('home_addr')
            }
          })

          
          // this.customizeData = {...this.customizeDataAll}
          this.setGroupOpt()
          this.setCityOpt() 
          this.setPlanOpt()
          this.checkQueryAndSetOpt()
        }else{  //如果不是從 filter result 頁面回來 
          getCustomizeFilter(this.$store.state.userInfo.id).then(result => { 
                //如果有回傳值
            if(result.body && result.body.field_name && result.body.field_name !== ''){  
              let customizeAry = result.body.field_name.split('-')
              for(let filterName in this.customizeDataAll){
                customizeAry.forEach(item => {
                  if(this.customizeDataAll[filterName].indexOf(item) !== -1){
                    this.customizeData[filterName].push(item)
                  }
                })
              }
            }else{  //如果回傳空物件
              this.customizeData = {...this.customizeDataDefault}
            }
            this.setGroupOpt()
            this.setCityOpt() 
            this.setPlanOpt()
            this.checkQueryAndSetOpt()
          })

        }
      })
      
    },
    setGroupOpt() {
      getGroupOfAgnt(this.$store.state.custInfo.ID, this.$store.state.userInfo.id).then(result => {
        if(result.body.groups) {
          this.grpOpt = result.body.groups.map(eachGroup => {
            return {
              val: eachGroup.grp_id,
              text: eachGroup.grp_name
            }
          })
        } else {
          this.grpOpt = []
        }
      })
    },
    setCityOpt() {
      this.cityOpt = this.Cities.map(item => item.city)
    },
    setPlanOpt() {
      //主約
      queryPlanCode(this.$store.state.userInfo.id, '1').then(result => {
        if(result.body.contract) {
          this.mainPlanOpt = result.body.contract.map(item => {
            return {
              val: item.PROD_ID,
              text: `${item.PROD_NM} (${item.PROD_ID})`
            }
          })
        } else {
          this.mainPlanOpt = []
        }
      })
      //副約
      queryPlanCode(this.$store.state.userInfo.id, '2').then(result => {
        if(result.body.contract) {
          this.secondaryPlanOpt = result.body.contract.map(item => {
            return {
              val: item.PROD_ID,
              text: `${item.PROD_NM} (${item.PROD_ID})`
            }
          })
        } else {
          this.secondaryPlanOpt = []
        }
      })
    },
    checkLogin() {
      var key = this.$store.state.userTokenKey
      if(!key.length > 0) {
        this.$router.push({path: '/login'})
      }
    },
    deleteFavoriteItem () {
      this.panelClose('favoriteModalOpen')
      this.panelClose('confirmDeleteOpen')
      deleteFavorite(this.$store.state.userInfo.id,this.deleteItem.favorite_name).then(result => {
        if(result.header.status === '200'){
          this.initSearchFavorite()
          this.$toast.info({title: `已刪除「${this.deleteItem.favorite_name}」`})
        }else{
          this.$toast.info({title: `更新失敗 訊息:${reslut.header.message}`})
        }
      })
    },
    deleteConfirm (form) {
      this.deleteItem = form
      this.panelOpen('confirmDeleteOpen')
    },
    openFavorite (favo) {
      this.selectedFavo = favo
      this.panelOpen('favoriteModalOpen')
    },
    submitFavorite (faForm,faExtraAge,faAreaOpt,newName,oldName) {
      // set filter here
      
      const form = faForm
      const formItem = this.formSelect
      const extraAge = faExtraAge   // modal 回傳參數
      const grpOpt = this.grpOpt
      const areaOpt = faAreaOpt      // modal 回傳參數    
      const mainPlanOpt = this.mainPlanOpt
      const secondaryPlanOpt = this.secondaryPlanOpt
      let filterAry = []

