            }else{
              filterOption = setMOption + setAOption
            }
            filterAry.push({title:`未投商品:${setMain} ${setAtta}`,field_name:'prod_codes_n',option:filterOption})
          }
          
        }
      }
      
      let field_name = []
      let option = []
      let option_ch = []
      let favorite_name = newName
      let original_favorite_name = oldName

      filterAry.forEach(item => {
        field_name.push(item.field_name)
        option.push(item.option)
        option_ch.push(item.title)
      })

      field_name = field_name.join('-')
      option = option.join('-')
      option_ch = option_ch.join(';')

      modifyFavorite(this.$store.state.userInfo.id,{field_name,option,option_ch,favorite_name,original_favorite_name}).then(result => {
        if(result.header.status === '200'){
          this.initSearchFavorite()
          this.$toast.info({title: `已更新「${this.selectedFavo.favorite_name}」`})
        }else{
          this.$toast.info({title: `更新失敗 訊息:${result.header.message}`})
        }
        this.panelClose('favoriteModalOpen')

      })
      
      
    },
    submitFilter (form) {
      // set filter here
      let filterItem = []
      for (let key in form){
        filterItem = [...filterItem,...form[key]]
      }
      if(filterItem.length <= 15){ // 條件選項不能多於15
        filterItem = filterItem.join('-')
        modifyCustomizeFilter(this.$store.state.userInfo.id,filterItem).then(result => {
          if(result.header.status === '200'){
            this.resetformData()
            this.formSelectItem.idage = ''
            this.customizeData = form
            this.$toast.info({title: '編輯成功'})
          }else{
            this.$toast.info({title: `編輯失敗 訊息:${result.header.message}`})
          }
          this.panelClose('customizeModalOpen')
        })
      }else{
        this.$toast.info({title: '條件不得超過 15項'})
        this.panelClose('customizeModalOpen')
      }
      
      
