      let optionAry = item.option.split('-')
      let CHAry = item.option_ch.split(';')
      fieldAry.forEach((field,index) => {
        recordAry.push({})
        recordAry[index].field_name = field
      })
      optionAry.forEach((option,index) => {
        recordAry[index].option = option
      })
      CHAry.forEach((option_ch,index) => {
        recordAry[index].title = option_ch
      })
      // console.log(recordAry);
      this.$router.push({ path: '/filter-result', query: recordAry })
