<script>
import {commonMixin} from '../../mixins/common'
export default {
  name: "CustomizeFilter",
  mixins: [commonMixin],
  props: {
    open: {
      type: Boolean,
      default: false
    },
    customizeData: [String,Array,Object],
  },
  watch: {
    open: 'initForm',
    form: {
      handler:function(){
        for(let item in this.form){
          if(this.form[item].length === this.customizeDataAll[item].length && this.formTitle.indexOf(item) === -1){
            this.formTitle.push(item)
          }else if(this.form[item].length !== this.customizeDataAll[item].length && this.formTitle.indexOf(item) > -1){
            this.formTitle = this.formTitle.join(',').replace(item,'').split(',')
          }
        }
      },
      deep:true,
    },
    formTitle: function (newAry,oldAry) {   //點選標題全選全消功能
      console.log('點選標題formTitle:',newAry,oldAry);
      if(newAry.length > oldAry.length){
        let index = newAry.length -1
        let key = newAry[index] 
        this.form[key] = this.customizeDataAll[key]
      }
      // else{
      //   oldAry.forEach(item => {
      //     if(newAry.indexOf(item) === -1){
      //       this.form[item] = []
      //     }
      //   })
      // }
    }
  },
  data () {
    return {
      form: {
        age: [],
        info: [],
        financial: [],
        service: [],
        isInsure: [],
        notInsure: [],
        futurePayment: [],
        payment: [],
      },
      formTitle: [],
      customizeDataAll:{   //過濾資料用
        age: ['idage','birthmonth','agemonth'],
        info: ['sex','home_addr','groups','custrl','marry','edu','job'],
        financial: ['incomy','family_incom','invest','finance','house','car'],
        service: ['web_member','online','mobile','payment','email','trans_plpy','ecall','policy_loan','claimoffset'],
        isInsure: ['prod_codes_main','prod_codes_atta'],
        notInsure: ['prod_codes_n_main','prod_codes_n_atta'],
        futurePayment: ['matage_month','annuity_month'],
        payment: ['pla1_month','plc1_month','plf1_month'],
      },
    }
  },
  methods: {
    initForm () {
      this.form = {...this.customizeData}
    },
    clickToggleAll(itemName){
      console.log('clickToggleAll: ',itemName,this.formTitle.indexOf(itemName));
      if(this.formTitle.indexOf(itemName) === -1){
        // this.form[itemName] = [...this.customizeDataAll[itemName]]
      }else{
        this.form[itemName] = []
      }
    },
  }
}
</script>
